<template>
  <v-container class="pt-0 px-2 pb-14">
    <v-row
      v-if="$store.state.isExistWorkoutBottomSheet"
      class="px-8"
      style="border-radius: 5px; background-color: #e0e0e0; diplay: flex"
    >
      <!-- justify-content: space-between; -->
      <!-- justify="center" -->
      <div
        class="notice pa-3 font-weight-medium"
        align="center"
        style="flex-grow: 1"
      >
        ÏßÑÌñâ Ï§ëÏù∏ ÏõåÌÅ¨ÏïÑÏõÉÏù¥ ÏûàÏùÑ Í≤ΩÏö∞, <br />
        ÏÉà ÏõåÌÅ¨ÏïÑÏõÉÏùÑ ÏãúÏûëÌïòÍ±∞ÎÇò <br />
        ÏÉà Î£®Ìã¥ÏùÑ ÎßåÎì§ Ïàò ÏóÜÏäµÎãàÎã§ üßôüèª‚Äç‚ôÇÔ∏è
      </div>
      <div style="display: flex; justify-content: center; align-items: center">
        <div class="text-h4">{{ fingerUp }}</div>
      </div>
    </v-row>
    <v-row class="py-5">
