<script>
import RoutineCard from "@/components/RoutineCard";
import { BUS } from "@/plugins/EventBus";

export default {
  components: {
    RoutineCard,
  },
  data() {
    return {
      routines: [],
      groupedRoutines: [],
      cardFullSize: false,
    };
  },
  computed: {
    fingerUp() {
      const arr = ["üëÜüèª", "üëÜüèº", "üëÜüèΩ", "üëÜüèæ", "üëÜüèø"];
      function getRandomIntInclusive(min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
      }
      return arr[getRandomIntInclusive(0, 4)];
    },
  },
  created() {
    this.loadRoutineData();
  },

  mounted() {
    BUS.$on("reloadRoutineData", () => {
      this.routines = [];
      this.groupedRoutines = [];
      this.loadRoutineData();
    });
  },
  methods: {
    async loadRoutineData() {
      const userUuid = this.$store.state.userUuid;
      if (!userUuid) return;
      try {
        const res = await this.$http.get(`/api/routine/${userUuid}`);
        if (res.data.success == true) {
          this.routines = res.data.rows;
          this.groupingRoutines();
        } else {
          this.$store.dispatch("popToast", {
            msg: `Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.(401) ${err}`,
            color: "error",
          });
          console.log(err);
        }
      } catch (err) {
        this.$store.dispatch("popToast", {
          msg: `Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.(500) ${err}`,
          color: "error",
        });
        console.log(err);
      }
    },
    groupingRoutines() {
      let initGroup = {
        routineGroupName: "",
        routineGroupUuid: "",
        exercises: [],
      };
      let newGroup = {
        routineGroupName: "",
        routineGroupUuid: "",
        exercises: [],
      };
      this.routines.forEach((oneOfSet, index) => {
        if (newGroup.routineGroupUuid !== oneOfSet.routineGroupUuid) {
          // newGroupÏùò uuidÏôÄ roofÏùò uuidÍ∞Ä Îã§Î•º Í≤ΩÏö∞
          if (newGroup.routineGroupUuid !== "") {
            // newGroupÏùò uuidÍ∞Ä ÏûàÏùÑ Í≤ΩÏö∞
            // => Í∏∞Ï°¥Ïóê ÎßåÎì† newGroupÏùÑ groupedRoutinesÏóê push
            // => newGroupÏùÑ init(clone)
            // newGroupÏùò uuidÍ∞Ä ÏóÜÏùÑ Í≤ΩÏö∞Îäî indexÍ∞Ä 0Ïù∏ Í≤ΩÏö∞ÏôÄ Í∞ôÏúºÎØÄÎ°ú initÏùÑ Ìï† ÌïÑÏöîÍ∞Ä ÏóÜÏùå
            this.groupedRoutines.push(newGroup);
            newGroup = Object.assign({}, initGroup);
            newGroup.exercises = []; // Object.assignÏúºÎ°ú deep cloneÏù¥ ÏïàÎêòÍ∏∞ ÎïåÎ¨∏
          }
          newGroup.routineGroupName = oneOfSet.routineGroupName;
          newGroup.routineGroupUuid = oneOfSet.routineGroupUuid;
          newGroup.exercises.push(oneOfSet);
        } else {
          // newGroupÏùò uuidÏôÄ roofÏùò uuidÍ∞Ä Í∞ôÏùÄ Í≤ΩÏö∞ => newGroupÏóê push
          newGroup.exercises.push(oneOfSet);
        }
        if (index === this.routines.length - 1) {
          // ÎßàÏßÄÎßâ Ïù∏Îç±Ïä§Ïùº Í≤ΩÏö∞ push
          this.groupedRoutines.push(newGroup);
        }
      });
    },
    makeNewRoutine() {
      this.$store.dispatch("createWorkoutBottomSheet", "create");
      this.$store.dispatch("showWorkoutBottomSheet");
    },
    async deleteRoutine(routineGroupUuid) {
      if (!confirm("Î≥µÍµ¨Í∞Ä Î∂àÍ∞ÄÎä•Ìï©ÎãàÎã§. Í∑∏ÎûòÎèÑ ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå? üßôüèª‚Äç‚ôÇÔ∏è")) {
        return;
      }
      try {
        const res = await this.$http.delete(`/api/routine/${routineGroupUuid}`);
        if (res.data.success == true) {
          this.$store.dispatch("popToast", {
            msg: `Î£®Ìã¥ÏùÑ ÏÇ≠Ï†úÌñàÏäµÎãàÎã§ üßôüèª‚Äç‚ôÇÔ∏è`,
            color: "primary",
          });
          this.routines = this.routines.filter((routine) => {
            return routine.routineGroupUuid !== routineGroupUuid;
          });
          this.groupedRoutines = [];
          this.groupingRoutines();
        } else {
          this.$store.dispatch("popToast", {
            msg: `Î£®Ìã¥ ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.(401) ${err}`,
            color: "error",
          });
          console.log(err);
        }
      } catch (err) {
        this.$store.dispatch("popToast", {
          msg: `Î£®Ìã¥ ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.(500) ${err}`,
          color: "error",
        });
        console.log(err);
      }
    },
  },
};
</script>

